<template>
  <div class="upload-box">
    <input type="file" @change="onFileChange" />
    <button @click="upload">آپلود</button>

    <div v-if="imageUrl" class="preview">
      <p>تصویر آپلود شد:</p>
      <img :src="imageUrl" alt="uploaded" style="max-width: 300px;" />
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { sendApi } from '@/utils/api';

const selectedFile = ref(null);
const imageUrl = ref('');

function toBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

const onFileChange = async (e) => {
  const file = e.target.files[0];
  if (!file) return;
  selectedFile.value = await toBase64(file); // رشته base64
};

const upload = async () => {
  if (!selectedFile.value) {
    alert('لطفاً یک فایل انتخاب کنید.');
    return;
  }

  try {
    const response = await sendApi(
      JSON.stringify({
        action: 'upload_image',
        data: selectedFile.value
      })
    );

    if (response.status === 'success' && response.url) {
      imageUrl.value = response.url;
    } else {
      alert(response.message || 'آپلود موفق نبود');
    }
  } catch (err) {
    console.error(err);
    alert('مشکلی در آپلود پیش آمد');
  }
};
</script>

<style scoped>
.upload-box {
  padding: 1rem;
}
.preview {
  margin-top: 1rem;
}
</style>
